@tailwind base;
@tailwind components;
@tailwind utilities;

/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&family=Space+Grotesk:wght@300;400;500;600;700&family=Space+Mono:wght@400;700&display=swap');

:root {
  /* Core Colors */
  --color-black: #0a0a0b;
  --color-white: #fff;
  --color-dark-grey: #717171;
  --color-mid-grey: #aaa;
  --color-grey: #b6cbcb;
  
  /* Vibrant Brand Colors for Nightlife */
  --color-dark-purple: #2d1b4e;
  --color-mid-pink: #ff84e4;
  --color-light-pink: #ffd1e7;
  --color-mid-purple: #d987ff;
  --color-light-purple: #e8d4ff;
  --color-mid-yellow: #ffe03d;
  --color-light-yellow: #f6fd87;
  --color-mid-orange: #ff965a;
  --color-coral: #ef8172;
  --color-neon-cyan: #00f5d4;
  --color-electric-blue: #7400b8;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--color-black);
  color: var(--color-white);
  overflow-x: hidden;
}

code {
  font-family: 'Space Mono', source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* Typography Classes */
.font-display {
  font-family: 'Space Grotesk', sans-serif;
}

.font-mono {
  font-family: 'Space Mono', monospace;
}

@layer base {
  :root {
    --background: 0 0% 4%;
    --foreground: 0 0% 98%;
    --card: 0 0% 6%;
    --card-foreground: 0 0% 98%;
    --popover: 0 0% 6%;
    --popover-foreground: 0 0% 98%;
    --primary: 300 100% 76%;
    --primary-foreground: 0 0% 4%;
    --secondary: 0 0% 14.9%;
    --secondary-foreground: 0 0% 98%;
    --muted: 0 0% 14.9%;
    --muted-foreground: 0 0% 63.9%;
    --accent: 300 100% 76%;
    --accent-foreground: 0 0% 4%;
    --destructive: 0 62.8% 30.6%;
    --destructive-foreground: 0 0% 98%;
    --border: 0 0% 14.9%;
    --input: 0 0% 14.9%;
    --ring: 300 100% 76%;
    --radius: 0.5rem;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
  }
}

/* Custom Animations */
@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

@keyframes pulse-glow {
  0%, 100% { box-shadow: 0 0 20px rgba(255, 132, 228, 0.3); }
  50% { box-shadow: 0 0 40px rgba(255, 132, 228, 0.6); }
}

@keyframes gradient-shift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@keyframes slide-up {
  from { opacity: 0; transform: translateY(30px); }
  to { opacity: 1; transform: translateY(0); }
}

@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}

.animate-float {
  animation: float 6s ease-in-out infinite;
}

.animate-pulse-glow {
  animation: pulse-glow 3s ease-in-out infinite;
}

.animate-gradient {
  background-size: 200% 200%;
  animation: gradient-shift 8s ease infinite;
}

.animate-slide-up {
  animation: slide-up 0.8s ease-out forwards;
}

.animate-fade-in {
  animation: fade-in 0.6s ease-out forwards;
}

/* Swiper Custom Styles */
.swiper {
  width: 100%;
  height: 100%;
}

.swiper-pagination-bullet {
  background: rgba(255, 255, 255, 0.4) !important;
  width: 12px !important;
  height: 12px !important;
  transition: all 0.3s ease !important;
}

.swiper-pagination-bullet-active {
  background: var(--color-mid-pink) !important;
  width: 32px !important;
  border-radius: 6px !important;
}

.swiper-button-next,
.swiper-button-prev {
  color: var(--color-mid-pink) !important;
  width: 50px !important;
  height: 50px !important;
  background: rgba(0, 0, 0, 0.5) !important;
  border-radius: 50% !important;
  backdrop-filter: blur(10px) !important;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  font-size: 20px !important;
  font-weight: bold !important;
}

/* Scrollbar Styles */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--color-black);
}

::-webkit-scrollbar-thumb {
  background: var(--color-mid-pink);
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-mid-purple);
}

/* Selection */
::selection {
  background: var(--color-mid-pink);
  color: var(--color-black);
}